'use client';

import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { useRouter } from 'next/navigation';

import { Button } from '@/components/ui/button';
import { Form, FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useToast } from '@/hooks/use-toast';
import { formSchema, type FormValues, type Block } from '@/lib/template-utils';
import { saveTemplateAction } from '@/lib/actions/template-actions';
import { BlockEditor } from '@/components/block-editor'; 
import { CertificateEditor } from '@/components/certificate-editor';

// Definición del tipo Template para que coincida con la estructura de la base de datos
export type Template = {
  id_plantilla: number;
  nombre: string;
  asunto_predeterminado: string;
  contenido: Block[] | string; // Puede ser un array de bloques o un JSON string
  categoria: 'Certificado' | 'Marketing' | 'Notificación' | 'Otro';
  fecha_creacion: string;
};

export function TemplateEditorClient({ template }: { template: Template | null }) {
  const router = useRouter();
  const { toast } = useToast();

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: template?.nombre || '',
      subject: template?.asunto_predeterminado || '',
      category: template?.categoria || 'Otro',
      blocks: Array.isArray(template?.contenido) ? template.contenido : [],
      certificate: !Array.isArray(template?.contenido) ? template?.contenido as string : '',
    },
  });

  const category = form.watch('category');

  async function onSubmit(data: FormValues) {
    const templateData = {
      ...data,
      // Si la categoría es 'Certificado', el contenido es el string del editor de certificados.
      // Si no, es el array de bloques del editor de bloques.
      content: data.category === 'Certificado' ? data.certificate : data.blocks,
    };

    try {
      const savedTemplate = await saveTemplateAction(template?.id_plantilla ?? null, templateData);
      toast({ title: 'Éxito', description: 'Plantilla guardada correctamente.' });
      router.push(`/templates/editor?id=${savedTemplate.id_plantilla}`);
      router.refresh();
    } catch (error) {
      console.error('Error al guardar la plantilla:', error);
      toast({ variant: 'destructive', title: 'Error', description: 'No se pudo guardar la plantilla.' });
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Nombre de la plantilla</FormLabel>
                <FormControl>
                  <Input placeholder="Ej: Bienvenida de nuevo usuario" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="subject"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Asunto del correo</FormLabel>
                <FormControl>
                  <Input placeholder="Ej: ¡Bienvenido a nuestra plataforma!" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="category"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Categoría</FormLabel>
                <Select onValueChange={field.onChange} defaultValue={field.value}>
                  <FormControl>
                    <SelectTrigger>
                      <SelectValue placeholder="Selecciona una categoría" />
                    </SelectTrigger>
                  </FormControl>
                  <SelectContent>
                    <SelectItem value="Marketing">Marketing</SelectItem>
                    <SelectItem value="Notificación">Notificación</SelectItem>
                    <SelectItem value="Certificado">Certificado</SelectItem>
                    <SelectItem value="Otro">Otro</SelectItem>
                  </SelectContent>
                </Select>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        {category === 'Certificado' ? (
          <FormField
            control={form.control}
            name="certificate"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Diseño del Certificado</FormLabel>
                <FormControl>
                  <CertificateEditor value={field.value} onChange={field.onChange} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        ) : (
          <FormField
            control={form.control}
            name="blocks"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Contenido del Correo</FormLabel>
                <FormControl>
                  <BlockEditor value={field.value} onChange={field.onChange} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        )}

        <div className="flex justify-end">
          <Button type="submit" disabled={form.formState.isSubmitting}>
            {form.formState.isSubmitting ? 'Guardando...' : 'Guardar Plantilla'}
          </Button>
        </div>
      </form>
    </Form>
  );
}
